import { AxiosError } from 'axios'
import { useSnackbar } from 'notistack'
import { useEffect, useState } from 'react'
import { useAuthHeader } from 'react-auth-kit'
import { Helmet } from 'react-helmet-async'
import { useQueryClient } from 'react-query'
import { Navigate, useNavigate, useParams } from 'react-router'
import { Link } from 'react-router-dom'
import { useRecoilValue } from 'recoil'

import {
  Box,
  Button,
  Card,
  Container,
  Divider,
  Grid,
  Stack,
  Tab,
  Tabs,
  Typography,
  useMediaQuery,
  useTheme
} from '@mui/material'

import { AUTH_USER_MESSAGE, WITHDRAWAL_USER_MESSAGE } from 'src/data/messages'
import { FIRST_SEMESTER, FIRST_SEMESTER_NAME, SECOND_SEMESTER_NAME } from 'src/data/semester'

import type { ClubMemberForAuth } from 'src/models/clubMember'

import ConfirmDialog from 'src/components/ConfirmDialog'
import DateForm from 'src/components/DateForm'
import ErrorMessageBox from 'src/components/ErrorMessageBox'
import MyPageReviewCard from 'src/components/MypageReviewCard'
import PageTitleWrapper from 'src/components/PageTitleWrapper'

import { useLogout } from 'src/hooks/api/authHooks'
import { deleteClubWithdrawal, useGetMemberProfile } from 'src/hooks/api/member'
import { useDeleteReview, useGetUserReviews } from 'src/hooks/api/review'
import useSafeAuthUser from 'src/hooks/useSafeAuthUser'
import { dateState } from 'src/recoil/atom'

import ConferenceTable from './ConferenceTable'
import UserProfile from './UserProfile'

interface TabPanelProps {
  children?: React.ReactNode
  index: number
  value: number
}

function TabPanel(props: TabPanelProps) {
  const { children, value, index, ...other } = props

  return (
    <div
      role="tabpanel"
      hidden={value !== index}
      id={`simple-tabpanel-${index}`}
      aria-labelledby={`simple-tab-${index}`}
      {...other}
    >
      {value === index && <Box sx={{ p: 1 }}>{children}</Box>}
    </div>
  )
}

const ProfilePage: React.FC = () => {
  const { id } = useParams()
  const clubId = Number(id)
  const theme = useTheme()
  const isMobile = useMediaQuery(theme.breakpoints.down('sm'))
  const [isDialog, setIsDialog] = useState<boolean>(false)
  const [tabIndex, setTabIndex] = useState<number>(0)
  const date = useRecoilValue(dateState)
  const authHeader = useAuthHeader()
  const { enqueueSnackbar } = useSnackbar()
  const navigate = useNavigate()
  const { userInfo, isLoggedIn } = useSafeAuthUser()
  const logoutMutation = useLogout(WITHDRAWAL_USER_MESSAGE.WITHDRAWAL_SUCCESS)
  const { data: profile, isError: isGetError } = useGetMemberProfile(clubId, date)
  const { data: reviews } = useGetUserReviews()
  const deleteReviewMutation = useDeleteReview(clubId)
  const queryClient = useQueryClient()

  const club = userInfo?.clubs.find(c => c.id === clubId) as ClubMemberForAuth
  const admin = userInfo?.clubs.find(
    c => c.id === clubId && (c.permission === 'SUPERADMIN' || c.permission === 'ADMIN')
  )
  const myReview = reviews?.find(r => r.clubId === clubId)

  const reviewProps = myReview
    ? {
        ...myReview,
        name: club?.name,
        userNickname: userInfo?.nickname ?? ''
      }
    : undefined

  const [isReviewDeleteDialogOpen, setIsReviewDeleteDialogOpen] = useState<boolean>(false)

  const handleDeleteReview = () => {
    if (myReview) {
      deleteReviewMutation.mutate({ reviewId: myReview.id })
      setIsReviewDeleteDialogOpen(false)
    }
  }

  const handleDelete = async () => {
    try {
      await deleteClubWithdrawal({ clubId, token: authHeader() })

      logoutMutation.mutate()
    } catch (err) {
      if (err instanceof AxiosError) {
        enqueueSnackbar(err?.response?.data?.message ?? WITHDRAWAL_USER_MESSAGE.SERVER_ERROR, {
          variant: 'error'
        })
      } else {
        enqueueSnackbar(WITHDRAWAL_USER_MESSAGE.UNKNOWN_ERROR, {
          variant: 'error'
        })
      }

      setIsDialog(false)
    }
  }

  const handleTabChange = (event: React.SyntheticEvent, newValue: number) => {
    setTabIndex(newValue)
  }

  useEffect(() => {
    if (!club) {
      navigate(`/club/${clubId}`)
    }

    queryClient.invalidateQueries('memberProfile')
    if (isGetError) {
      navigate(`/club/${clubId}`)
    }
  }, [date])

  if (isLoggedIn && !userInfo) {
    enqueueSnackbar(AUTH_USER_MESSAGE.WRONG_AUTH_STATE, { variant: 'error' })
    return <Navigate to="/" />
  }

  return (
    <>
      <Helmet>
        <title>프로필</title>
      </Helmet>
      {club && (
        <PageTitleWrapper>
          <Grid container alignItems="center">
            <Grid item>
              <Box display="flex" flexDirection={isMobile ? 'column' : 'row'} alignItems={isMobile ? '' : 'center'}>
                <Link to={`/club/${clubId}`} style={{ textDecoration: 'none' }}>
                  <Typography variant="h3" component="h3" marginRight="15px" gutterBottom>
                    {club.name}
                  </Typography>
                </Link>
                {admin && (
                  <Link to={`/manage/${clubId}/member`} style={{ marginBottom: '5px', textDecoration: 'none' }}>
                    <Typography variant="body1" fontWeight={'bold'} color="primary">
                      동아리 관리 바로가기
                    </Typography>
                  </Link>
                )}
              </Box>
              <Typography variant="subtitle2">
                {userInfo?.nickname} 님이 {club.name}에서 활동한 내역입니다
              </Typography>
            </Grid>
          </Grid>
        </PageTitleWrapper>
      )}
      <Container maxWidth="lg">
        <DateForm club={club} />

        {profile ? (
          <Card>
            <Tabs value={tabIndex} onChange={handleTabChange} sx={{ margin: 2 }}>
              <Tab label="내 정보" />
              <Tab label="출석" />
              <Tab label="후기" />
            </Tabs>
            <Divider />
            <TabPanel value={tabIndex} index={0}>
              <UserProfile profile={profile} />
            </TabPanel>
            <TabPanel value={tabIndex} index={1}>
              <ConferenceTable conferences={profile.conferenceRounds} />
            </TabPanel>
            <TabPanel value={tabIndex} index={2}>
              <Box display="flex" flexDirection="column" alignItems="center" p={2} gap={2}>
                {reviewProps ? (
                  <Stack spacing={2} alignItems="center">
                    <MyPageReviewCard {...reviewProps} onDelete={() => setIsReviewDeleteDialogOpen(true)} />
                    <Button
                      variant="contained"
                      onClick={() => {
                        navigate(`/club/${clubId}/review`, { state: { review: myReview } })
                      }}
                    >
                      활동 후기 수정하기
                    </Button>
                  </Stack>
                ) : (
                  <Button
                    variant="contained"
                    onClick={() => {
                      navigate(`/club/${clubId}/review`)
                    }}
                  >
                    동아리 활동 후기 작성하기
                  </Button>
                )}
              </Box>
            </TabPanel>
          </Card>
        ) : (
          <ErrorMessageBox
            children={`${date.year}년도 ${
              date.semester === FIRST_SEMESTER ? FIRST_SEMESTER_NAME : SECOND_SEMESTER_NAME
            }에는 존재하는 활동내역이 없습니다. 다른학기를 선택해 주세요.`}
          />
        )}
      </Container>
      <Box textAlign="center" mt={3}>
        <Button
          variant="contained"
          color="error"
          onClick={() => {
            setIsDialog(true)
          }}
        >
          동아리 탈퇴
        </Button>
      </Box>
      <ConfirmDialog
        open={isReviewDeleteDialogOpen}
        handleClose={() => {
          setIsReviewDeleteDialogOpen(false)
        }}
        confirmMessage="정말로 후기를 삭제하시겠습니까?"
        handleSubmit={handleDeleteReview}
      />
      <ConfirmDialog
        open={isDialog}
        handleClose={() => {
          setIsDialog(false)
        }}
        confirmMessage={WITHDRAWAL_USER_MESSAGE.WITHDRAWAL_CONFIRM}
        handleSubmit={handleDelete}
      />
    </>
  )
}

export default ProfilePage
